import { BaseElement, createClassName, defineElement, html, query } from '@umetrics/sartorius-ui-base';
import { footerStyles } from './footerStyle';
import { FooterSlot } from './footerProperties';
import { version } from './metadata';

@defineElement({ tagName: 'footer', styles: footerStyles, version })
export class Footer extends BaseElement {
    // Static members
    // Private members

    @query('[class$="-left-content"]', true) private _leftContent;
    @query('footer') private _footer;

    private _resizeObserver: ResizeObserver | null = null;
    private _maxWidth = 0;

    // Protected members

    // Public members

    constructor() {
        super();
        this._handleResize = this._handleResize.bind(this);
    }

    disconnectedCallback() {
        this._uninstallResizeObserver();
        if (this._resizeObserver !== null) {
            this._resizeObserver.disconnect();
        }
        super.disconnectedCallback();
    }

    protected firstUpdated() {
        this._installResizeObserver();
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {});
        return html` <footer class="${rootClasses}">
            <div class="${baseClassName}-left-content">
                <slot
                    @slotchange="${this._handleSlotChange}"
                    name="${FooterSlot.BUTTON}"
                    class="${baseClassName}-button-slot"
                ></slot>
            </div>
            <div class="${baseClassName}-right-content">
                <slot name="${FooterSlot.RIGHT}" class="${baseClassName}-right-slot"></slot>
            </div>
        </footer>`;
    }

    private _handleSlotChange() {
        const leftContent = this.shadowRoot!.querySelector('[class$="-left-content"]');
        const rightContent = this.shadowRoot!.querySelector('[class$="-right-content"]');
        this._maxWidth = rightContent
            ? leftContent!.clientWidth + 106 + rightContent.clientWidth
            : leftContent!.clientWidth + 106;
    }

    private _handleResize() {
        const slot = this._leftContent.querySelector('[class$="-button-slot"]');
        const buttons = slot.assignedElements();
        if (this._maxWidth >= this._footer.clientWidth) {
            buttons.forEach((button) => {
                button.slim = true;
            });
            this._leftContent.style.gap = 16 + 'px';
        } else {
            buttons.forEach((button) => {
                button.slim = false;
            });
            this._leftContent.style.gap = 100 + 'px';
        }
    }

    private _uninstallResizeObserver() {
        if (this._resizeObserver !== null && this._footer) {
            this._resizeObserver.unobserve(this._footer);
        }
    }

    private _installResizeObserver() {
        if (this._resizeObserver === null) {
            this._resizeObserver = new ResizeObserver(this._handleResize);
        }
        this._resizeObserver.observe(this._footer);
    }
}
