import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
} from '@umetrics/sartorius-ui-base';
import { FooterButtonProperties, FooterButtonSlot } from './footerButtonProperties';
import { version } from './metadata';
import { footerButtonStyles } from './footerButtonStyle';

@defineElement({ tagName: 'footer-button', styles: footerButtonStyles, version })
export class FooterButton extends BaseElement implements FooterButtonProperties {
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    slim = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    disabled = false;

    @query('button') // Selector to find input tag
    private _button;

    constructor() {
        super();
        this.addEventListener('click', this._onClick);
    }

    focus(options?: FocusOptions) {
        this._button.focus(options);
    }

    blur() {
        this._button.blur();
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            disabled: this.disabled,
            slim: this.slim,
        });
        const labelSlot = this.slim ? html`` : html`<span class="${baseClassName}-label"><slot></slot></span>`;
        return html`
            <button class="${rootClasses}" ?disabled="${this.disabled}">
                <div class="${baseClassName}-icon">
                    <slot name="${FooterButtonSlot.ICON}"></slot>
                </div>
                ${labelSlot}
            </button>
        `;
    }

    private _onClick(e: Event) {
        if (this.disabled) {
            e.stopImmediatePropagation();
        }
    }
}
