import { css, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const footerButtonStyles: GenerateStyles = (tagName) => {
    const { root, is, child } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }
            :host {
                display: inline-block;
            }

            ${root} {
                border: 1px solid transparent;
                border-radius: 0;
                cursor: pointer;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                font-size: 1rem;
                height: 80px;
                outline: 0px;
                appearance: button;
                position: relative;
                transition: all 0.1s ease-in;
                text-align: center;
                text-decoration: none;
                text-transform: none;
                width: 144px;
                vertical-align: top;
                line-height: normal;
                font-weight: normal;
                background-color: transparent;
                color: ${cssVariableAsVar('color_primary_2')};
            }
            ${is('slim')} {
                width: 48px;
                height: 48px;
            }

            ::slotted([slot='icon-slot']) {
                font-size: 1.875rem;
            }
            ${is('slim')} ::slotted([slot="icon-slot"]) {
                font-size: 1.5rem;
            }
            ${is('slim')} ${child('icon')} {
                padding-bottom: 0px;
            }

            ${child('icon')} {
                padding-bottom: 10px;
            }

            ${is('!disabled')}:hover {
                background-color: var(--button-hover-color, #ffffff26);
                color: ${cssVariableAsVar('color_primary_2')};
            }
            ${root}:active {
                background-color: #ffffff40 !important;
            }
            ${root}:focus-visible:before {
                content: ' ';
                display: block;
                position: absolute;
                inset: 1px;
                border: 2px solid ${cssVariableAsVar('color_primary_2')};
            }
            ${is('disabled')} {
                background: transparent;
                color: #ffffff50;
                cursor: not-allowed;
                pointer-events: none;
            }
        `,
    ];
};
