import { css, CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const footerStyles: GenerateStyles = (tagName) => {
    const { root, child } = cssSelector(tagName);
    return [
        style,
        css`
            :host {
                width: 100%;
            }

            ${root} {
                height: 136px;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                background-color: ${cssVariableAsVar('color_primary_3')};
                color: ${cssVariableAsVar('color_primary_2')};
                padding: 0px 24px 0px 82px;
                box-sizing: border-box;
                font-size: 14px;
            }

            ${child('left-content')} {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 100px;
            }
            ${child('right-content')} {
                padding-left: 100px;
            }

            ::slotted([slot='right-slot']) {
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-end;
                font-size: 12px;
                line-height: 18px;
                color: ${cssVariableAsVar('color_secondary_3')};
            }
        `,
    ] as CSSResultArray;
};
